.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main-background{
  margin-top: 10px;
  background-image: url("img/background.png");
  width: 100%;
  height: 250px;
}

.main-p{
  padding-top: 20px;
  font-size: 2rem;
  font-weight: 500;
  font-family: 'Jua', sans-serif;
  line-height: 200%;
}

.detail-p{
  line-height: 50%;
}

.search{
  margin-top:30px;
}

.patch{
  display: inline-block;
  margin-top: 20px;
  border: solid 2px;
  width: 95%;
  border-radius: 6px 6px 6px 6px;
}

.note{
  display: inline-block;
}

.table-cell{
  margin-left: 20px;
  margin-right: 20px;
  height: 30px;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  border: solid 1px skyblue;
}

.patch-p{
  margin-top: 15px;
}